// JavaScript
import './scss/main.scss';
import './scripts/main';
// Styles
import '../node_modules/owl.carousel/dist/owl.carousel.min';
require('bootstrap');
require('owl.carousel');
$(document).ready(function() {
    $(".loading").hide();
    $('.owl-carousel').owlCarousel({
        loop: false,
        margin: 10,
        nav: true,
        item: 3,
        responsive: {
            0: {
                items: 1
            },
            600: {
                items: 3
            },
            1000: {
                items: 5
            }
        }
    })

});